/*----------------------------------------------------------

BREAKPOINT MIXINS

-----------------------------------------------------------*/


/** breakpoints for js **/


html:before, body:before {


    display:none;


    content: "mobile";
}


@media (min-width: $(tablet)) {


    html:before, body:before {


        content:"tablet";
    }
}


@media (min-width: $(desktop)) {


    html:before, body:before {


        content:"desktop";
    }
}


